@import "base";

body {
    background-color: #172d13;
    color: white;
    margin: 0 auto;
    background-image: url("/assets/images/backgrounds_pattern_transparent.png");
    background-repeat: repeat;
    background-size: 150px;
    font-size: 20px;
    padding: 0px;
}

.narrow {
    max-width: 950px;
    min-height: 100vh;
    text-align: center;
    margin: 0 auto;
    padding: 20px;
    background-color: #172d13;
}

/* ----------------------------------------------------------- Fonts ----------------------------------------------------------- */

@font-face {
    font-family: 'Newsreader Variable';
    src: url('/assets/fonts/newsreader/fonts/variable/woff/Newsreader%5Bopsz,wght%5D.woff') format('woff'),
         url('/assets/fonts/newsreader/fonts/variable/woff2/Newsreader%5Bopsz,wght%5D.woff2') format('woff2');
    font-weight: 100 900; /* Define the weight range */
    font-style: normal; /* You can also define italic styles if available */
}

body {
    font-family: 'Newsreader Variable', sans-serif;
    font-weight: 400; /* Normal weight */
}

h1 {
    font-family: 'Newsreader Variable', sans-serif;
    font-weight: 700; /* Bold weight */
}

h2 {
    font-family: 'Newsreader Variable', sans-serif;
    font-weight: 600; /* Semi-bold weight */
}

h4 {
    font-family: 'Newsreader Variable', sans-serif;
    font-weight: 600; /* Semi-bold weight */
}

p {
    font-family: 'Newsreader Variable', sans-serif;
    font-weight: 400; /* Light weight */
}

em {
    font-family: 'Newsreader Variable', sans-serif;
    font-style: italic; /* If the variable font supports italic */
    font-weight: 400; /* Normal weight for italic */
}

/* ----------------------------------------------------------- Fonts ----------------------------------------------------------- */

/* ----------------------------------------------------------- Links ----------------------------------------------------------- */

a {
    color: white;
    text-decoration: none;
    /* Remove underline from links */
    /* Default link color */
}

a:visited {
    color: white;
    /* Color after the link has been visited */
}

a:hover {
    color: #e4312b;
    /* Color when the mouse is over the link */
}

a:active {
    color: #e4312b;
    /* Color when the link is clicked */
}

/* ----------------------------------------------------------- Links ----------------------------------------------------------- */

/* ----------------------------------------------------------- Button ---------------------------------------------------------- */

.button-container {
    display: flex; /* Use flexbox for layout */
    flex-direction: column; /* Arrange buttons in a column */
    align-items: center; /* Center buttons horizontally */
    gap: 20px; /* Space between buttons */
    margin: 20px 0; /* Space above and below the button container */
}

.button {
    background-color: white;
    border: none;
    color: #479459;
    padding: 10px 20px; /* Adjusted padding for better appearance */
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 25px;
    border-radius: 20px;
    font-weight: 700; /* Bold weight */
    width: 90%;
}

.button a {
    color: #479459;
}

.button a:visited {
    color: white;
    /* Color after the link has been visited */
}

.button a:hover {
    color: #e4312b;
    /* Color when the mouse is over the link */
}

.button a:active {
    color: #e4312b;
    /* Color when the link is clicked */
}

/* ----------------------------------------------------------- Button ---------------------------------------------------------- */

/* ----------------------------------------------------------- Posts ----------------------------------------------------------- */

.image_post {
    width: 100%;
    max-width: 300px;
}

p img {
    display: block;
    /* Makes the image a block element */
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    /* Center the image if desired */
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
}

.event {
    overflow: hidden;
    text-align: center;
}

.event img {
    width: 100%;
    height: auto;
}

.article {
    overflow: hidden;
    text-align: center;
}

.article img {
    width: 100%;
    height: auto;
}

.news {
    overflow: hidden;
    text-align: center;
}
.news img {
    width: 100%;
    height: auto;
}

.news h4 {
    margin-top: 0; /* Remove top margin */
    margin-bottom: 5px; /* Adjust this value as needed */
}

.news p {
    margin-top: 0; /* Remove top margin */
    margin-bottom: 30px;
}

/* ----------------------------------------------------------- Posts ----------------------------------------------------------- */

/* ---------------------------------------------------------- Columns ---------------------------------------------------------- */

.container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; /* Allow items to wrap to the next line */
}

.column {
    width: 30%;
}

@media screen and (max-width: 768px) {
    .container {
        flex-direction: column; /* Change to column layout */
        align-items: stretch; /* Ensure columns take full width */
    }

    .column {
        width: 100%; /* Make each column take full width */
    }
}

/* ---------------------------------------------------------- Columns ---------------------------------------------------------- */


/* ---------------------------------------------------------- Top Nav ---------------------------------------------------------- */

/* Add a black background color to the top navigation */
.topnav {
    background-color: #479459;
    overflow: hidden;
    margin: 0; /* Remove margin */
    padding: 0; /* Remove padding */
}

/* Style the links inside the navigation bar */
.topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 25px;
    font-weight: 700; /* Bold weight */
}

/* Change the color of links on hover */
.topnav a:hover {
    background-color: #479459;
    color: #e4312b;
}

/* Add an active class to highlight the current page */
.topnav a.active {
    background-color: #479459;
    color: white;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
    display: none;
}

/* Remove space around the logo */
.topnav .logo img {
    display: block; /* Ensures no extra space below the image */
    height:50px
}

.topnav .logo {
    padding: 0; /* Remove padding */
    margin: 0; /* Remove margin if any */
    display: block; /* Ensure it behaves as a block element */
}


/* When the screen is less than 600 pixels wide, 
hide all links, except for the first one ("Home"). 
Show the link that contains should open and close the topnav (.icon) */
@media screen and (max-width: 768px) {
    .topnav a:not(:first-child) {
        display: none;
    }

    .topnav a.icon {
        float: right;
        display: block;
    }

    .topnav.responsive {
        position: relative;
    }

    .topnav.responsive a.icon {
        position: absolute;
        right: 0;
        top: 0;
    }

    .topnav.responsive a {
        float: none;
        display: block;
        text-align: center;
    }
}

/* ---------------------------------------------------------- Top Nav --------------------------------------------------------- */
























